*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --white_cl: #fff;
  --txt-light_cl: #907e82;
  --accent_cl: #d41443;
  --accent-dark_cl: #d41342;
  --accent-light_cl: #ffa5ba;
  --mobile-menu-bg: #e17992;
  --social-links-bg_cl: #fea5bb;
  --product-one_cl: #ffb8ca;
  --product-two_cl: #f0d1a5;
  --product-tree_cl: #c2e297;
  --bg-light_cl: #fff5f6;
  --btn-hover_cl: #ecebeb;
  --overlay-gradient: linear-gradient(to right, rgba(47, 48, 58, .4), rgba(47, 48, 58, .4));
  --dark-bg: rgba(0, 0, 0, .2);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: DM Sans, sans-serif;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, p {
  margin-top: 0;
  margin-bottom: 0;
}

svg {
  fill: currentColor;
}

@keyframes swing {
  15% {
    transform: translateX(5px);
  }

  30% {
    transform: translateX(-5px);
  }

  50% {
    transform: translateX(3px);
  }

  65% {
    transform: translateX(-3px);
  }

  80% {
    transform: translateX(2px);
  }

  100% {
    transform: translateX(0);
  }
}

@keyframes slide-top {
  from {
    transform: translateY(-5px);
  }

  to {
    transform: translateY(5px);
  }
}

@keyframes rotateY {
  0% {
    transform: rotateY(0deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

@keyframes shake {
  10%, 90% {
    border-color: var(--accent_cl);
    transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    border-color: var(--accent_cl);
    transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    border-color: var(--accent_cl);
    transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    transform: translate3d(4px, 0, 0);
  }
}

.container {
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (max-width: 767.5px) {
  .container {
    min-width: 320px;
    max-width: 100%;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.btn, .modal__button {
  min-width: 107px;
  max-height: 44px;
  letter-spacing: .03em;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 20px;
  padding: 13px 23px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.13;
  transition: color .35s cubic-bezier(.4, 0, .2, 1), background-color .35s cubic-bezier(.4, 0, .2, 1);
}

.btn--red-mode {
  color: var(--white_cl);
  background-color: var(--accent_cl);
}

.btn--light-mode {
  color: var(--accent_cl);
  background-color: var(--white_cl);
}

.btn--red-mode:hover, .btn--red-mode:focus {
  background-color: var(--accent-dark_cl);
  outline: rgba(0, 0, 0, 0);
}

.btn--light-mode:hover, .btn--light-mode:focus {
  background-color: var(--btn-hover_cl);
  outline: rgba(0, 0, 0, 0);
}

.section {
  padding: 60px 0;
}

.section-uppertitle, .section-title, .section-subtitle {
  text-transform: uppercase;
  font-family: Titan One, cursive;
}

.section-uppertitle {
  letter-spacing: 0;
  color: var(--accent-light_cl);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.16;
}

@media screen and (min-width: 768px) {
  .section-uppertitle {
    letter-spacing: .04em;
    font-size: 26px;
    font-weight: 400;
    line-height: 1.15;
  }
}

.section-subtitle {
  letter-spacing: .04em;
  color: var(--white_cl);
  font-size: 20px;
  font-weight: 400;
  line-height: 1.15;
}

@media screen and (min-width: 1200px) {
  .section-uppertitle, .section-subtitle {
    letter-spacing: .04em;
    font-size: 30px;
    font-weight: 400;
    line-height: 1.13;
  }
}

.section-title {
  letter-spacing: .04em;
  color: var(--accent_cl);
  font-size: 34px;
  font-weight: 400;
  line-height: 1.14;
}

@media screen and (min-width: 768px) {
  .section-title {
    letter-spacing: .06em;
    font-size: 48px;
    font-weight: 400;
    line-height: 1.14;
  }
}

@media screen and (min-width: 1200px) {
  .section-title {
    letter-spacing: .06em;
    font-size: 58px;
    font-weight: 400;
    line-height: 1.13;
  }
}

.page-header__menu-toggler {
  z-index: 3;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  top: 33px;
  right: 16px;
}

.page-header__menu-toggler.is-open {
  z-index: 10;
}

@media screen and (min-width: 768px) {
  .page-header__menu-toggler {
    top: 53px;
    right: 208px;
  }

  .page-header__menu-toggler.is-open {
    position: fixed;
    top: 26px;
    right: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .page-header__menu-toggler {
    display: none;
  }
}

.page-header__menu-toggler .page-header__icon-open {
  opacity: 1;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: -6px;
  right: -6px;
}

.page-header__menu-toggler .page-header__icon-close {
  opacity: 0;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: -6px;
  right: -6px;
}

.page-header__menu-toggler.is-open .page-header__icon-open {
  opacity: 0;
}

.page-header__menu-toggler.is-open .page-header__icon-close {
  opacity: 1;
}

@media screen and (max-width: 1199px) {
  .menu-container {
    z-index: 9;
    height: 100vh;
    background-color: var(--mobile-menu-bg);
    padding: 60px 0;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    top: 0;
    right: 20px;
    transform: translateX(20px);
  }
}

@media screen and (min-width: 1200px) {
  .menu-container {
    flex-direction: row-reverse;
    align-items: center;
    margin-left: auto;
    display: flex;
  }
}

@media screen and (max-width: 767px) {
  .menu-container.is-closed {
    overflow: hidden;
  }
}

@media screen and (max-width: 1199px) {
  .menu-container.is-closed {
    background-color: rgba(0, 0, 0, 0);
    transition: transform .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
    position: absolute;
    right: -10px;
    transform: translateX(calc(100% + 20px));
  }

  .menu-container.is-closed .navigation {
    opacity: 0;
  }
}

.page-header__button-buy {
  align-items: center;
  margin: 0 20px;
  padding: 12px 48px 12px 61px;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  transform: translate(0);
}

@media screen and (max-width: 767.5px) {
  .menu-container.is-closed .page-header__button-buy {
    opacity: 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199.5px) {
  .page-header__button-buy {
    margin: 0 34px;
    padding: 12px 43px;
  }

  .menu-container.is-closed .page-header__button-buy {
    padding: 12px 37px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
    transform: translate(-248px, -27px);
  }
}

@media screen and (min-width: 1200px) {
  .page-header__button-buy {
    z-index: 5;
    margin: 0 0 0 123px;
    padding: 11px 37px;
    font-size: 16px;
    line-height: 21px;
    transform: translate(0);
  }
}

.page-header {
  padding: 8px 20px;
  animation-name: loaded-heder;
  animation-duration: 1s;
  position: relative;
}

@media screen and (min-width: 768px) {
  .page-header {
    padding-top: 32px;
    padding-left: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .page-header {
    padding-top: 37px;
    padding-bottom: 0;
    padding-left: 16px;
    display: flex;
  }
}

@keyframes loaded-heder {
  0% {
    opacity: 0;
    transform: translateY(-180px);
  }

  40% {
    opacity: 0;
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.page-heading {
  color: var(--white_cl);
  letter-spacing: .04em;
  z-index: 4;
  opacity: 0;
  font-family: Titan One, cursive;
  font-size: 26px;
  line-height: 30px;
  animation-name: loaded;
  animation-duration: .8s;
  animation-delay: .2s;
  animation-fill-mode: forwards;
  position: relative;
  transform: scale(.8)translate(-180px, -40px);
}

@media screen and (min-width: 768px) {
  .page-heading {
    font-size: 22px;
    line-height: 25px;
  }
}

@media screen and (min-width: 1200px) {
  .page-heading {
    font-size: 38px;
    line-height: 44px;
  }
}

.page-heading__sub-text {
  color: var(--accent_cl);
  font-size: 48px;
  line-height: 90.5%;
}

@media screen and (min-width: 768px) {
  .page-heading__sub-text {
    font-size: 34px;
  }
}

@media screen and (min-width: 1200px) {
  .page-heading__sub-text {
    font-size: 58px;
  }
}

.page-header__icon--ml {
  margin-left: 27px;
}

@media screen and (min-width: 768px) {
  .menu-container.is-closed .page-header__icon--ml {
    margin-left: 13px;
  }
}

@media screen and (min-width: 1200px) {
  .page-header__icon--ml {
    margin-left: 13px;
  }
}

.back-drop {
  width: 100%;
  height: 100%;
  background-color: var(--dark-bg);
  z-index: 7;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (max-width: 767px) {
  .back-drop {
    padding: 15px;
  }
}

.back-drop.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.modal__content {
  z-index: 12;
  max-width: 528px;
  transform-origin: 0 100%;
  background-color: #ebccd3;
  border-radius: 20px;
  margin: auto;
  padding: 40px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 2px 4px 3px rgba(0, 0, 0, .12), 2px 4px 5px rgba(0, 0, 0, .14), 2px 4px 5px rgba(0, 0, 0, .2);
}

@media screen and (max-width: 1199px) {
  .modal__content {
    min-width: 320px;
    max-width: 450px;
    padding: 35px 20px 20px;
  }
}

.modal__content.is-hidden {
  opacity: 0;
  pointer-events: none;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
  transform: rotate(80deg);
}

.modal__close {
  width: 30px;
  height: 30px;
  color: var(--white_cl);
  background-color: var(--accent-light_cl);
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: color .35s cubic-bezier(.4, 0, .2, 1), background-color .35s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
  transform: translate(25%, -25%);
}

.modal__close:hover {
  color: var(--accent_cl);
  background-color: var(--white_cl);
}

.modal__title {
  text-align: center;
  color: var(--accent_cl);
  margin: 0 0 12px;
  font-size: 16px;
  line-height: 21px;
}

@media screen and (max-width: 767px) {
  .modal__title {
    font-size: 20px;
    line-height: 1.5;
  }
}

@media screen and (min-width: 1200px) {
  .modal__title {
    font-size: 22px;
    line-height: 1.5;
  }
}

.modal__filed {
  width: 100%;
  margin-bottom: 10px;
  display: inline-block;
  position: relative;
}

.modal__label {
  color: var(--txt-light_cl);
  letter-spacing: .01em;
  margin-bottom: 4px;
  font-size: 12px;
  line-height: 1.17167;
}

@media screen and (min-width: 1200px) {
  .modal__label {
    font-size: 16px;
  }
}

.modal__input {
  width: 100%;
  background-color: var(--white_cl);
  color: var(--modal-font-color);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 4px;
  margin-top: 5px;
  padding: 11px 12px 11px 42px;
  font-size: 14px;
  transition: border .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 1200px) {
  .modal__input {
    font-size: 16px;
  }
}

.modal__input:focus {
  outline: 1px solid var(--accent_cl);
  border: 1px solid var(--accent_cl);
}

.modal__form:not(.modal__form:focus-within) .modal__input:invalid {
  animation: shake .82s cubic-bezier(.36, .07, .19, .97) both;
}

.modal__icon {
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 35px;
  left: 14px;
}

.modal__input:focus + .modal__icon {
  color: var(--accent_cl);
}

.modal__input--comment {
  height: 50px;
  resize: none;
  padding: 12px 16px;
}

.modal__filed--checkbox {
  justify-content: center;
  align-items: center;
  margin-top: 10px;
  margin-bottom: 30px;
  display: flex;
}

.modal__icon-checkbox {
  width: 16px;
  height: 16px;
  background-color: var(--white_cl);
  border: 2px solid var(--white_cl);
  border-radius: 2px;
  margin-right: 7px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

.modal__checkbox:checked + .modal__icon-checkbox {
  background-color: var(--accent_cl);
  border: 2px solid var(--accent_cl);
}

.modal__form:not(.modal__form:focus-within) .modal__checkbox:invalid + .modal__icon-checkbox {
  animation: shake .82s cubic-bezier(.36, .07, .19, .97) both;
}

.modal__checkbox-label {
  color: var(--txt-light_cl);
  letter-spacing: .03em;
  cursor: pointer;
  line-height: 1.71429;
}

@media screen and (max-width: 767px) {
  .modal__checkbox-label {
    font-size: 12px;
    line-height: 1.16667;
  }
}

@media screen and (max-width: 1199px) {
  .modal__checkbox-label {
    font-size: 14px;
    line-height: 1.16667;
  }
}

.modal__link {
  color: var(--accent_cl);
}

.modal__button {
  color: var(--white_cl);
  margin: 0 auto;
  display: block;
}

@media screen and (min-width: 1200px) {
  .modal__button {
    font-size: 16px;
  }
}

.modal__input--country {
  padding-left: 14px;
}

.modal__decor {
  color: var(--accent_cl);
  font-size: 16px;
}

.btn-scroll-top {
  z-index: 4;
  width: 50px;
  height: 50px;
  color: var(--white_cl);
  background-color: var(--accent_cl);
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  border: none;
  border-radius: 50%;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), opacity 1s cubic-bezier(.4, 0, .2, 1), visibility 1s cubic-bezier(.4, 0, .2, 1);
  animation: slide-top 1s linear 2s infinite alternate both;
  position: fixed;
  bottom: 90px;
  right: 30px;
  box-shadow: 0 0 12px #f30c46;
}

.btn-scroll-top:hover, .btn-scroll-top:focus {
  background-color: #f30c46;
}

.btn-scroll-top__icon {
  margin: 15px;
  transform: rotate(-90deg);
}

.btn-scroll-top.is-hidden {
  visibility: visible;
  opacity: 1;
}

.hero__description-slide {
  opacity: 1;
  z-index: 1;
  transition: transform .4s cubic-bezier(.4, 0, .2, 1), opacity .4s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(0);
}

.show-first .hero__description-slide--first {
  opacity: 1;
  z-index: 2;
  transform: translateX(0);
}

.show-first .hero__description-slide--two {
  opacity: 0;
  z-index: 2;
  transform: translateX(100%);
}

.show-first .hero__description-slide--three {
  opacity: 0;
  z-index: 1;
  transform: translateX(-102%);
}

.show-two .hero__description-slide--first {
  opacity: 0;
  z-index: 2;
  transform: translateX(-102%);
}

.show-two .hero__description-slide--two {
  opacity: 1;
  z-index: 2;
  transform: translateX(0);
}

.show-two .hero__description-slide--three, .show-three .hero__description-slide--first {
  opacity: 0;
  z-index: 1;
  transform: translateX(100%);
}

.show-three .hero__description-slide--two {
  opacity: 0;
  z-index: 2;
  transform: translateX(-102%);
}

.show-three .hero__description-slide--three {
  opacity: 1;
  z-index: 2;
  transform: translateX(0);
}

.button-section {
  display: inline-block;
  position: relative;
}

.contacts__btn--efects {
  z-index: 3;
  position: relative;
}

.button-menu {
  width: 100%;
  height: 0;
  background-color: var(--white_cl);
  opacity: 0;
  z-index: 2;
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  padding: 54px 20px 10px;
  transition: height .6s ease-in-out, opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 0;
  box-shadow: 0 10px 15px rgba(0, 0, 0, .2);
}

.button-menu.is-hidden {
  height: 300px;
  opacity: 1;
  transition: height .6s ease-in-out, visibility .35s cubic-bezier(.4, 0, .2, 1), opacity .25s cubic-bezier(.4, 0, .2, 1);
}

.button-menu__item {
  transition: transform .6s ease-in-out;
  position: absolute;
  top: 0;
}

.button-menu.is-hidden .button-menu__item--one {
